/* place footer on bottom */

body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
}

main {
  flex: 1;
}

/*  ======== navbar styles =========  */
/* Add a black background color to the top navigation */
.topnav {
  background-color: #333;
  overflow: hidden;
  display: inline-block;
  text-align: center;
  padding: 12px 0;
  height: 2vh;
}

/* Style the links inside the navigation bar */
.topnav a {
  color: #f2f2f2;
  padding: 12px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}


/*  ============================  */

* { font-family: Blizzard,Helvetica,Arial,sans-serif; }
body { background-image: url('owl_banner.jpg'); }

main {
  padding: 1%;
  text-align: center;
}

.column {
  display: inline-grid;
  grid-template-columns: 400px;
}

table {
  width: 400px;
}
th, td {
  height: 40px;
  font-size: 24px;
}

.main-container { margin-top: 15px; }
.stats-container { margin-top: 20px; }
.main-button {
  padding: 8px 16px;
  display: inline-block;
  font-size: 20px;
  margin: 4px 2px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
  background-color: #1a1a1a;
  color: white;
}

.main-button:hover { color: #f89e14; }
#compare-container {min-height: 98vh;}

/*  ========= Table Styles =============  */

table, th, td {
  border-collapse: collapse;
  border-top: 1px solid #e8e8e8;
  border-bottom: 1px solid #e8e8e8;
}
th {
  color: #fdfdfd;
  background-color: #f89e14;
}
tr:nth-child(even) {background-color: #f2f2f2;}
tbody {
  background-color: white;
}


/*  ========= Player Avatar Styles =============  */

.player-card {
  border: 1px #e8e8e8 solid;
}

.player-avatar {
  display: grid;
  grid-template-columns: repeat(8, 50px);
  grid-template-rows: repeat(7, 50px);

}

.player-number {
  font-size: 40px;
  vertical-align: 10px;
}

.player-team, .player-role, .player-number, .close {
  grid-column: 8;
}
.player-number { grid-row: 5; }
.player-team { grid-row: 6; }
.player-role { grid-row: 7; }
.close {
  grid-row: 1;
  font-size: 40px;
}

.icon {
  border: 1px #e8e8e8 solid;
}

/*  ========= Add Player Form Styles =============  */

.add-player-forms {
  border: 1px solid #e8e8e8;
  padding: 20px 0;
  background-color: #d9d9d9;
}
.team-filter { margin-right: 10px; }
.player-select {
  margin: 10px 0;
  width: 200px;
}
.add-player-forms button {
  padding: 5px 10px;
  display: inline-block;
  font-size: 16px;
  margin: 0 5px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
}


/*  ========= Scatterplot Styles =============  */

svg circle {
  opacity: 0.8;
}
svg .tick line { stroke: #C0C0BB; }
svg .axis-label {
  font-size: 3.2em;
}
svg .tick text {
  font-size: 2.5em;
  fill: #635F5D;
}


.scatter-header { margin-top: 100px;}
.scatter-header h1 {
  font-size: 3em;
  fill: #635F5D;
}
.axisSelects {
  display: grid;
  justify-content: center;
  grid-template-columns: 220px 40px 220px;
}
.axisSelects * {
  margin: 0;
  padding: 5px;
}
#scatter-container { min-height: 90vh; }
.scatter-items {
  width: 1200px;
  background-color: rgba(231, 231, 231, 0.7);
  margin: auto;
}
